* {
  padding: 0;
  margin: 0;
  color: #fff;
  background-color: #000;
  /*  outline: 1px solid red; */
}

/* Header */
header {
  margin-top: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

a {
  text-decoration: none;
}

ul {
  display: flex;
}

ul li {
  margin-right: 30px;
  list-style: none;
}

#search {
  height: 30px;
  margin-right: 5px;
  border-bottom: solid 2px white;
}
